const sendPostMessage = (data, frame = null) => {
  if (frame) {
    frame.postMessage(data, '*')
  } else {
    parent.postMessage(data, '*')
  }
}

const __API__ = () => {
  window.addEventListener('message', (event) => {
    const eventData = event.data
    const { eventType, payload } = eventData
    switch (eventType) {
      case 'RECEIVE_PIQ_GROOVE_CONFIGS':
        return APICallback.setConfigs(payload)
    }
  })
}

const APICallback = {
  setConfigs (payload) {
    window._PaymentIQ_GROOVE_API_CONFIGS_ = payload
  }
}

const _GET_CONFIGS_ = () => {
  sendPostMessage(
    {
      eventType: 'GET_PIQ_GROOVE_CONFIGS'
    })
}

const _SET_APP_HEIGHT_WIDTH_ = (payload) => {
  sendPostMessage(
    {
      eventType: 'SET_APP_HEIGHT_AND_WIDTH',
      payload
    })
}

export default {
  __API__,
  _GET_CONFIGS_,
  _SET_APP_HEIGHT_WIDTH_
}
