import { APP_STATE } from '../mutation-types'

const state = {
  formData: {
    subject: '',
    fullName: '',
    email: '',
    transactionId: '',
    category: '',
    issueOccuredDate: new Date().toISOString().replace('T', ' ').substr(0, 16),
    message: ''
  }
}

const mutations = {
  [APP_STATE.SET_FORM_DATA] (state, payload) {
    state.formData = {
      ...state.formData,
      ...payload
    }
  }
}

export default {
  state,
  mutations
}
