import Vue from 'vue'
import VueRouter from 'vue-router'
import authGuard from './auth-guard'
const Landing = () => import(/* webpackChunkName: "Landing" */ '@/pages/Landing.vue')
const ContactPage = () => import(/* webpackChunkName: "ContactPage" */ '@/pages/ContactPage.vue')
const Status = () => import(/* webpackChunkName: "Status" */ '@/pages/Status.vue')

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Landing',
    component: Landing
  },
  {
    path: '/contact',
    name: 'ContactPage',
    component: ContactPage
  },
  {
    path: '/status',
    name: 'Status page',
    component: Status
  }
]

const AppRouter = new VueRouter({
  base: '/piq-groove-widget/dist/',
  mode: 'history',
  routes
})

AppRouter.beforeEach(authGuard)

export default AppRouter
